* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.calculator {
  height: 100vh;
  background-color: #222;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.screen {
  width: 90%;
  height: 15%;
  background-color: #666;
  border-radius: 10px;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px;
  display: flex;
}

.screen input {
  width: 100%;
  height: 100%;
  text-align: right;
  color: #fff;
  background-color: #666;
  border: none;
  font-size: 36px;
}

.buttons {
  width: 90%;
  height: 70%;
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.buttons button {
  width: 23%;
  height: 20%;
  color: #222;
  cursor: pointer;
  background-color: #f2f2f2;
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  font-size: 24px;
  display: flex;
}

.buttons .operator {
  color: #fff;
  background-color: #ff9500;
}

.buttons button:active {
  box-shadow: none;
  transform: translateY(2px);
}

